

a {
    color: var(--color-black);
}

.header {
    font-size: 16px;
    height: 62px;
    .container-adapt {
        @include  flex-all-sb;
        a {
            align-items: center;
            display: flex;
            p {
                margin: 0;
            }
        }
    }
}

.container-adapt {
    @include flex-all-sb;
    margin: 0 auto;
    height: 62px;
    max-width: 90%;
}
.logo {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 28px;
    text-align: center;
}
.logo-img {
    height: 46px;
    width: 100%;
    margin-right: 5px;
}
.menu {
    display: flex;
}
.nav {
    @include flex-all-sb;
    font-weight: medium;
}
.nav-item {
    cursor: pointer;
    &:not(:last-child){
        margin-right: 40px;
    }
    &:last-child {
        margin-right: 20px;
    }

}



/* Hover underline effect */
.hover-underline-animation {
    display: inline-block;
    position: relative;
    color:var(--color-black);
  }
  
  .hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: var(--color-black);
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .hover-underline-animation:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }


/* END of Hover underline effect */


.btn-black{
    margin-left: 20px;
    padding: 10px 32px;
    color: var(--color-white);
    background-color: var(--color-black);
    border-radius: 4px;
    box-sizing:border-box;
    border: 2px transparent solid; 
    transition: 0.2s ease-in-out;
    &:hover {
        border: 2px var(--color-black) solid; 
        background-color: var(--color-white);
        color: var(--color-black);
        transition: 0.2s ease-in-out;
    }
}




