.title-section {
    height: 735px;
    background-image:url('../img/BGImage.jpg');
    background-size: cover;
}
.title-section__container{
    position: relative;
    height: 100%;
}
.title-content__wrapper {
    position: inherit;    
    top: 128px;
    left: 110px;
    max-width: 640px;
}
.main-title {
    font-weight: bold;
    font-size: 64px;
    color: var(--color-white);
    margin-bottom: 48px;
}
.btns-wrapper {
    display: flex;
    align-items: center;
}
.what-we-do-btn {
    margin-right: 24px;
}
.btn-video {
    font-weight: 500;
    display: flex;
    align-items: center;
    padding: 12px 16px;
    color: var(--color-white);
    transition: 0.2s ease-in-out;
    svg{
        margin-right: 8px;
        fill: var(--color-white);
        transition: 0.2s ease-in-out;
    }
    &:hover {
        transition: 0.2s ease-in-out;
        color: var(--color-hover);
        svg {
            fill: var(--color-hover);
        }
    }
}


.statistics {
    position: absolute;
    width: 90%;
    bottom: 63px;
    display: flex;
    justify-content: space-between;
    color: var(--color-white);
    margin-left: 110px;
    font-weight: 500;
    letter-spacing: 1px;
    hr {
        color: var(--color-white);
        height: 0.5px;
        background-color:var(--color-white);
        width: 60%;
        transform: translateY(16px);
        border: 0;
    }
}